.dancing {
  font-family: 'Dancing Script', cursive;
}

body {
  min-height: 100vh;
}

@mixin phone {
  @media screen and (max-width: 768px) {
    @content;
  }
}

@mixin reset-position {
  position: relative;
  top: initial;
  right: initial;
  transform: translate(0);
  left: initial;
  height: auto;
}

.full-screen {
  width: 100%;
  height: 100vh;
  overflow-x: hidden;

  @include phone() {
    height: auto;
  }
}

.section-bg {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.overlay {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.charmonman {
  font-family: 'Charmonman', cursive;
}

.section-head {
  .section-bg {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
  }

  .text-content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    color: #fff;
    font-size: 1.5em;
    padding: 20px;
    min-width: 10em;

    &::before, &::after {
      content: '';
      width: 10000px;
      background-color: #fff;
      height: 1px;
      position: absolute;
      top: 50%;
    }

    &::before {
      right: 100%;
    }

    &::after {
      left: 100%;
    }
  }

  .overlay {
    background-color: hsla(0, 0%, 0%, 0.6);
  }

  h1 {
    margin-bottom: 0;
  }

  .line {
    position: absolute;
    width: 40%;
    height: 1px;
    background: #fff;
    top: 50%;

    &.left {
      right: 60%;
    }

    &.right {
      left: 60%;
    }
  }

  @include phone() {
    height: 100vh;
  }
}

.section-image {
  overflow: hidden;

  .section-bg {
    position: absolute;
    top: -10px;
    left: -10px;
    right: -10px;
    bottom: -10px;
    opacity: 0.75;
    filter: blur(10px);
  }

  .image-wrapper {
    position: absolute;
    height: 80%;
    top: 50%;
    transform: translateY(-50%);
    left: 20%;

    img {
      height: 100%;
      border: 1px solid #fff8;
    }
  }

  .overlay {
    background: hsla(0, 0%, 0%, 0.4);
  }
}

.section-intro {
  .text-content {
    color: #fff;
    position: absolute;
    right: 20%;
    width: 30%;
    padding-left: 30px;
    top: 50%;
    transform: translateY(-50%);
    font-family: 'Montserrat', sans-serif;

    h2 {
      font-size: 2.5em;
    }
  }

  @include phone() {
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-wrap: wrap;
    padding: 50px;

    .image-wrapper {
      position: relative;
      @include reset-position();
      flex-basis: 100%;

      img {
        width: 100%;
      }
    }
    
    .text-content {
      position: relative;
      width: initial;
      padding-left: 0;
      top: initial;
      right: initial;
      transform: translate(0);
      text-align: center;
      margin-top: 50px;
    }
  }
}

