.section-slide {
  .text-content {
    position: absolute;
    width: 100%;
    text-align: center;
    top: 20%;
  }

  ul {
    position: absolute;
    bottom: 0%;
    left: 10%;
    right: 10%;
    top: 10%;
    margin: 0;
    padding: 0;
  }

  li {
    position: absolute;
    list-style: none;
    width: 35%;
    left: 15%;
    bottom: 0;
    transition: all 0.3s cubic-bezier(0.075, 0.82, 0.165, 1);
    img {
      width: 100%;
      border: 1px solid #fff;
      box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1),
        0 10px 10px -5px rgba(0, 0, 0, 0.04);
    }

    &:nth-child(1) {
      transform: translate(70%, -35%) rotate(10deg);
    }

    &:nth-child(2) {
      transform: translate(50%, -25%) rotate(7deg);
    }

    &:nth-child(3) {
      transform: translate(30%, -15%) rotate(3deg);
    }

    &:nth-child(4) {
      transform: translate(0%, -5%) rotate(-10deg);
    }

    &.show {
      z-index: 100;
      width: 80%;
      left: 10%;
      transform: translate(0, -5%) rotate(0deg);
    }
  }

  .text-content {
    text-align: center;
    font-family: 'Charmonman', cursive;
  }

  .link {
    font-family: 'Amatic SC', cursive;
    position: absolute;
    right: 2%;
    bottom: 10px;

    img {
      filter: invert(1);
      transform: translateY(10%) rotate(-55deg);
      height: 150px;
      display: block;
    }

    a {
      display: block;
      bottom: 0;
      color: #fff;
      font-weight: bold;
      font-size: 1.2em;
      padding: 10px 15px;
    }
  }

  @include phone() {
    height: 100vh;

    ul {
      bottom: 30%;
      transform: translateX(-10%);
    }

    li {
      width: 60%;
    }
  }
}
